import mion from './mion.png';
import styles from './styles.module.css';
import { Button } from '@consta/uikit/Button';
import { IconAdd } from '@consta/uikit/IconAdd';

export const Chats = () => (
  <>
    <div className={styles.container}>
      <img src={mion} className={styles.logo}></img>
      <Button label='Новый чат' view='secondary' iconLeft={IconAdd} size='l' className={styles.btn}/>
    </div>
  </>
);