import { Card } from "@consta/uikit/Card";
import { Text } from "@consta/uikit/Text";
import styles from './style.module.css'

export const ChatRow = ({ speaker, text }) => {
    if (speaker === 'user') return (
      <Card verticalSpace="s" horizontalSpace="5xl" className={styles.userRow}>
        <div className={styles.row}>
          <div className={styles.userAvatar}>
            <p>МЮ</p>
          </div>
          <Text size="2xl" className={styles.text}>
            {text}
          </Text>
        </div>
      </Card>
    );
    else if (speaker === 'mion') return (
      <Card verticalSpace="s" horizontalSpace="5xl" className={styles.mionRow}>
        <div className={styles.row}>
          <div className={styles.mionAvatar}>
            <p>МИ</p>
          </div>
          <Text size="2xl" className={styles.text}>
            {text}
          </Text>
        </div>
      </Card>
    ); 
};