import { Text } from '@consta/uikit/Text';
import styles from './styles.module.css';
import logo from './mion_logo.png';
import { IconSmile } from '@consta/uikit/IconSmile';
import { ThemeToggler } from '@consta/uikit/ThemeToggler';
import Toggler from '../../LandingPage/night.png';

const items = ['Default'];

export const Menu = () => (
  <div className={styles.menu}>
    <hr></hr>
    <div className={styles.menuItem}>
      <Text size="3xl">
        <IconSmile className={styles.iconSmile} size="m" />
        Личный кабинет
      </Text>
    </div>
    <div className={styles.menuItem}>
      <Text size="3xl">&#x21aa; Стратегия</Text>
    </div>
    <div className={styles.menuItem}>
      <Text size="3xl">&#x2605; Ресурс</Text>
    </div>
    <div className={styles.menuItem}>
      <Text size="3xl">&#x2661; Менеджмент</Text>
    </div>
    <div className={styles.menuItem}>
      <Text size="3xl">
        <img src={logo}></img>Мион
      </Text>
    </div>
    <img src={Toggler} className={styles.toggler}></img>
    <ThemeToggler items={items} value={items[0]} />
  </div>
);
